import { Component, OnInit } from '@angular/core';
import {BaseApiService} from '../shared/api-service/base-api.service';
import {DialogService} from '../shared/dialog-service/dialog.service';
import {Ng2FileDropAcceptedFile} from 'ng2-file-drop';

@Component({
    selector: 'app-learn',
    templateUrl: './learn.component.html',
    styleUrls: ['./learn.component.scss'],
    providers: [BaseApiService, DialogService]
})
export class LearnComponent {

    public image: string;
    public name: string;
    private supportedFileTypes: string[] = ['image/png', 'image/jpeg', 'image/gif'];
    private imageShown = false;
    private currentProfileImage = 'assets/images/grafty-logo.png';
    constructor(private apiService: BaseApiService, private dialogsService: DialogService) {
    }

    private dragFileAccepted(acceptedFile: Ng2FileDropAcceptedFile) {
        const fileReader = new FileReader();
        fileReader.onloadend = () => {
            this.currentProfileImage = fileReader.result;
            this.imageShown = true;
            this.image = fileReader.result.substr(23);
        };
        fileReader.readAsDataURL(acceptedFile.file);
    }
    private train() {
        if (this.image === undefined || this.image === '') {
            this.dialogsService.alert('Input image ' , 'Please add the image you want to train.');
            return;
        }
        if (this.name === undefined || this.name === '') {
            this.dialogsService.alert('Input Name ' , 'Please fill the name input above.');
            return;
        }
        const body = {
            command: 'fr-train-add',
            facename: this.name,
            image: this.image
        };
        console.log(body);
        this.apiService.frPost('train', body).subscribe(response => {
            console.log('Response' , response);
            if (JSON.parse(response._body).result === 'success') {
                this.dialogsService.alert('Done', 'Recognizing done');
            } else if (JSON.parse(response._body).result === 'fail') {
                this.dialogsService.alert('Fail', 'Recognizing failed - No face detected');
            }
        }, error => {
            console.log('Error', error);
        });
    }
}



